  .diy-header{
    display: flex;
    align-items: center;
    width: auto;
    height: 85px;
    background: #f6f6f6;
  }
  .diy-header-h3{
   color: #003459;
  }
  .diy-sidebar{
    height: 570px;
    width: 136px;
    background: #003459;
    position: absolute;
    top: 225px;
    z-index: 100;
    border-radius: 0 16px 16px 0;
    -webkit-box-shadow: -1px 0px 9px 4px rgba(0,0,0,0.68); 
    box-shadow: -1px 0px 9px 4px rgba(0,0,0,0.68);
    /* padding: 16px 10px 10px 28px; */
    
  }
  .diy-sidebar{
   /* padding: 10px 10px 10px 10px; */
 display: flex;
 justify-content: center;
  }
  .diy-sidebar a{
    text-decoration: none;
  }
  .diy-sidebar-div{
    /* position: relative; */
    /* z-index: 10; */
    box-shadow: #000;
    
  }
  .diy-icon{
    padding: 16px;
    display: flex;
   justify-content: center;
   align-items: center;
   cursor: pointer;
   
  
 
 
  }
