  .download-container{
    width: 100%;
    display: inline-block;
    vertical-align: middle;
  }
  .download-container div{
    position: absolute;
  right: 150px;  
  width: auto;
  height: auto;
  display: flex;
  }
  .diy-sidebar-item{
    display: flex;
    justify-content: center;
  }
  .download-pdf-png{
    width: 242px;
    height: 60px;
    background: transparent;
    border: 2px solid black;
    border-radius: 10px;
    margin-left: 10px;
  }
  .place-order {
    background-color: #003459;
    color: #fff;
    text-transform: uppercase;
    font-size: 24px;
    border: 2px solid #003459;
  }
  .place-order:hover{
    color: #fff;
  }
  .arrow{
    margin-left: 5px;
    margin-top: -2px;
  }
  .inactiveOnMobile{
    position: absolute;
    width: 100%;
    visibility: hidden;
  }
  @media only screen and (max-width: 980px) {
    .inactiveOnMobile{
      background-color: white;
      visibility: visible;
      height: 200vh;
      z-index: 101;
      color: #003459;
    }
  }