#prevBtn, #nextBtn{
  width: 38px;
  height: 38px;
  border-radius: 50%;
  background: rgba(229, 229, 229, 0.6);
  padding: 8px;
  mix-blend-mode: normal;
opacity: 0.54;
}
#prevBtn1{
  width: 38px;
  height: 38px;
  border-radius: 50%;
  background: white;
  padding: 8px;
  mix-blend-mode: normal;
opacity: 0.54;
}

@media screen and (max-width: 480px) {
  .margin-10{
